"use client";

import { useEffect, useState } from "react";
import { useRouter, useSearchParams } from "next/navigation";
import { getDiscoverMovies } from "@/api/tmdb";
import { MovieCard } from "@/components/MovieCard";
import { Filters } from "@/components/Filters";
import { AdvancedFilters } from "@/components/AdvancedFilters";
import { ChevronLeft, ChevronRight, Loader2 } from "lucide-react";
import clsx from "clsx";

export default function MoviesPage() {
    const [movies, setMovies] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);
    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, setTotalPages] = useState(1);
    const searchParams = useSearchParams();
    const router = useRouter();

    useEffect(() => {
        async function loadMovies() {
            setLoading(true);
            const genre = searchParams.get("with_genres");
            const page = parseInt(searchParams.get("page") || "1");
            const sortBy = searchParams.get("sort_by") || "popularity.desc";
            const yearMin = searchParams.get("year_min");
            const yearMax = searchParams.get("year_max");
            const runtime = searchParams.get("runtime");
            const language = searchParams.get("language");
            const certification = searchParams.get("certification");
            
            setCurrentPage(page);
            
            const filters: any = { sort_by: sortBy, page };
            if (genre) filters.with_genres = genre;
            if (yearMin) filters["primary_release_date.gte"] = `${yearMin}-01-01`;
            if (yearMax) filters["primary_release_date.lte"] = `${yearMax}-12-31`;
            if (runtime) {
                const [min, max] = runtime.split("-").map(Number);
                filters["with_runtime.gte"] = min;
                filters["with_runtime.lte"] = max;
            }
            if (language) filters.with_original_language = language;
            if (certification) {
                filters.certification_country = "US";
                filters.certification = certification;
            }
            
            const data = await getDiscoverMovies(filters);
            if (data?.results) {
                setMovies(data.results);
                setTotalPages(Math.min(data.total_pages || 1, 500));
            }
            setLoading(false);
        }
        loadMovies();
    }, [searchParams]);

    const handlePageChange = (newPage: number) => {
        if (newPage < 1 || newPage > totalPages || loading) return;
        setLoading(true);
        const params = new URLSearchParams(searchParams.toString());
        params.set("page", newPage.toString());
        router.push(`/movies?${params.toString()}`);
        window.scrollTo({ top: 0 });
    };

    return (
        <main className="min-h-screen pt-32 sm:pt-36 pb-20 bg-bg-main">
            <div className="container mx-auto px-4 sm:px-6 lg:px-20 mb-6 sm:mb-8">
                <div className="flex items-center justify-between">
                    <div>
                        <h1 className="text-2xl sm:text-3xl md:text-4xl font-display font-bold text-white mb-2">Movies</h1>
                        <p className="text-text-secondary text-sm sm:text-base">Discover the best movies from around the world.</p>
                    </div>
                    <AdvancedFilters />
                </div>
            </div>

            <Filters />

            <div className="container mx-auto px-4 sm:px-6 lg:px-20 mt-8 sm:mt-10">
                {loading ? (
                    <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-4 sm:gap-6">
                        {[...Array(10)].map((_, i) => (
                            <div key={i} className="aspect-[2/3] bg-white/5 rounded-xl animate-pulse" />
                        ))}
                    </div>
                ) : (
                    <>
                        <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-y-10 sm:gap-y-12 gap-x-3 sm:gap-x-6">
                            {movies.map((movie) => (
                                <MovieCard key={movie.id} movie={movie} />
                            ))}
                        </div>

                        {/* Pagination */}
                        {totalPages > 1 && (
                            <div className="flex items-center justify-center gap-2 mt-16 mb-8">
                                <button
                                    onClick={() => handlePageChange(currentPage - 1)}
                                    disabled={currentPage === 1 || loading}
                                    className="p-3 rounded-lg bg-white/5 border border-white/10 text-white hover:bg-white/10 disabled:opacity-30 disabled:cursor-not-allowed transition-all flex items-center justify-center min-w-[44px]"
                                >
                                    {loading ? <Loader2 size={20} className="animate-spin" /> : <ChevronLeft size={20} />}
                                </button>

                                <span className="text-white px-4 py-2 font-medium">
                                    Page {currentPage} of {totalPages}
                                </span>

                                <button
                                    onClick={() => handlePageChange(currentPage + 1)}
                                    disabled={currentPage === totalPages || loading}
                                    className="p-3 rounded-lg bg-white/5 border border-white/10 text-white hover:bg-white/10 disabled:opacity-30 disabled:cursor-not-allowed transition-all flex items-center justify-center min-w-[44px]"
                                >
                                    {loading ? <Loader2 size={20} className="animate-spin" /> : <ChevronRight size={20} />}
                                </button>
                            </div>
                        )}
                    </>
                )}
            </div>
        </main>
    );
}
